@charset "utf-8";
// 引用基础框架样式库
@import "lib/all";
@function px2rem( $px) {
    @return $px*320/$designWidth/20+rem;
}

$designWidth: 750;
* {
    -webkit-box-sizing: border-box;
    box-sizing: border-box;
    -webkit-tap-highlight-color: transparent;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}

html,
body,
.wrapper,
.container {
    width: 100%;
    height: 100%;
    overflow: hidden;
}

.video-container video {
    width: 100%;
    height: auto;
    position: absolute;
    top: 50%;
    left: 0;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);
}

.content-container {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 9;
    .btn-set1,
    .btn-set2 {
        position: absolute;
        top: px2rem(974);
        left: 50%;
        width: px2rem(290);
        height: px2rem(80);
    }
    .btn-set1 {
        margin-left: - px2rem(290);
    }
}