@charset "utf-8";

//-----------------------------------------------------
// typography scss 
//-----------------------------------------------------

/* Typography */
.text-sm{
	font-size: 12px;
}

.text-gray{
	color: $font-color-light;
}

// Base price 
.base-price {
	font-size: 12px;
	line-height: 24px;

	b {
		font-size: 20px;
		color: #F60;
		font-weight: normal;
	}

	dfn {
		vertical-align: super;
	}
}
.price-sm{
	line-height: 20px;
	dfn {
		vertical-align: 3px;
	}

	b {
		font-size: 18px;
	}
}
.price-xsm{
	line-height: 14px;
	dfn {
		vertical-align: 1px;
	}

	b {
		font-size: 14px;
	}
}

// text content
.txt-ellipsis {
	display: inline-block;
	width: 100%;
	overflow: hidden;
	text-overflow: ellipsis;
}
